<!-- eslint-disable vuejs-accessibility/label-has-for -->
<template>
  <StepOne />
  <section class="card-group row w-tab">
    <div class="card col-7">
      <Loading :isLoading="isLoading" />
      <div class="card-body">
        <VForm v-slot="{ meta }" @submit="addOrder">
          <div class="mb-3 row">
            <label for="inputName" class="col-sm-3 col-form-label">Name</label>
            <div class="col-sm-5">
              <VField
                type="text"
                class="form-control"
                id="inputName"
                name="name"
                value=""
                rules="required"
              />
              <ErrorMessage name="name" class="text-danger" />
            </div>
          </div>
          <div class="mb-3 row">
            <label for="inputName" class="col-sm-3 col-form-label">Phone Number</label>
            <div class="col-sm-5">
              <VField
                type="text"
                class="form-control"
                id="inputName"
                name="tel"
                value="09xxxxxxx"
                rules="required"
              />
              <ErrorMessage name="tel" class="text-danger" />
            </div>
          </div>
          <div class="mb-3 row">
            <label for="inputAddress" class="col-sm-3 col-form-label">Address</label>
            <div class="col-sm-9">
              <VField
                type="text"
                class="form-control"
                id="staticEmail"
                name="address"
                value=""
                rules="required"
              />
              <ErrorMessage name="address" class="text-danger" />
            </div>
          </div>
          <div class="mb-3 row">
            <label for="inputEmail" class="col-sm-3 col-form-label">Email Address</label>
            <div class="col-sm-9">
              <VField
                type="text"
                class="form-control"
                id="inputEmail"
                name="email"
                value=""
                rules="required|email"
              />
              <ErrorMessage name="email" class="text-danger" />
            </div>
          </div>
          <button type="submit" class="btn btn-primary" :disabled="!meta.valid">送出</button>
        </VForm>
      </div>
    </div>
    <div class="card col-5">
      <div class="card-body">
        <div class="mb-3 h4 text-bolder">CHARTS</div>
        <template v-for="(item, id) in carts" :key="id">
          <div class="d-flex align-items-center border-bottom pb-3">
            <img :src="item.product.imageUrl" class="align-self-start" style="width: 30%" alt="" />
            <div class="ms-2">
              <h5>{{ item.product.title }}</h5>
              <p class="mb-0">價格：${{ item.product.price }}</p>
              <p class="mb-0">尺寸：8"</p>
            </div>
            <div class="ms-3">
              <select class="form-select form-select-sm" aria-label=".form-select-sm example">
                <option selected value="1">1</option>
                <option value="2">2</option>
                <option value="3">3</option>
              </select>
            </div>
            <div class="ms-3">
              <p class="mb-0">${{ $toCurrency(item.total) }}</p>
            </div>
          </div>
        </template>
      </div>
    </div>
  </section>
  <StepTwo />
</template>

<script>
import StepOne from '@/components/checkout/StepOne.vue';
import StepTwo from '@/components/checkout/StepTwo.vue';
import Loading from '@/components/models/LoadingModel.vue';

export default {
  data() {
    return {
      carts: {},
      isLoading: false,
    };
  },
  components: {
    Loading,
    StepOne,
    StepTwo,
  },
  methods: {
    addOrder(obj) {
      const api = `${process.env.VUE_APP_API}/api/${process.env.VUE_APP_PATH}/order`;
      this.$http
        .post(api, {
          data: {
            user: {
              name: obj.name,
              email: obj.email,
              tel: obj.tel,
              address: obj.address,
            },
            message: '這是留言',
          },
        })
        .then((res) => {
          console.log(res);
        });
    },
    getCarts() {
      this.isLoading = true;
      const api = `${process.env.VUE_APP_API}/api/${process.env.VUE_APP_PATH}/cart`;
      this.$http.get(api).then((res) => {
        this.carts = res.data.data.carts;
        this.isLoading = false;
      });
    },
  },
  mounted() {
    this.getCarts();
  },
};
</script>
